bar__brand h1 {\n    color: #0B2447;\n    text-transform: uppercase;\n    font-size: 22px;\n    \n    user-select: none;\n  }\n\n  .app-bar .app-bar__navigation {\n    position: absolute;\n    top: 50px;\n    left: -180px;\n    width: 150px;\n    transform: translateX(0);\n    padding: 20px 20px; \n    background-color: white;\n    overflow: hidden;\n    z-index: 50; \n    transition: left 0.3s; \n    min-height: 44px;\n    min-width: 44px;\n  }\n\n  .app-bar .app-bar__navigation.open {\n    left: 0;\n    z-index: 100;\n    height: 100vh;\n  }\n\n\n  .app-bar .app-bar__navigation ul li a {\n    display: flex;\n    text-decoration: none;\n    color: #0B2447;\n\n    margin-bottom: 2px;\n    min-width: 44px !important;\n    min-height: 44px !important;\n  }\n\n\n  .app-bar .app-bar__menu button,\n  .app-bar .app-bar__navigation ul li a {\n    width: 44px;\n    height: 44px;\n  }\n\n  \n\n\n  .app-bar .app-bar__menu button,\n  .app-bar .app-bar__navigation ul li a {\n    display: inline-block;\n    width: 44px !important;\n    height: 44px !important;\n  }\n\n\n  .app-bar .app-bar__navigation .coustum a{\n    display: inline-block;\n    min-width: 44px;\n    min-height: 44px;\n}\n\n  \n\n  /*\n    Main Content\n  */\n\n  main {\n    flex: 1;\n  }\n\n  .content {\n    margin: 0 auto;\n    min-height: 100%;\n  }\n\n  .content .content__heading {\n    font-weight: normal;\n  }\n\n\n  /*\n    restaurants\n  */\n\n  .restaurants {\n    display: grid;\n    grid-template-columns: 1fr;\n    gap: 16px;\n    margin: 32px 0;\n  }\n\n  /*\n    restaurant Item\n  */\n\n  .restaurant-item {\n    background-color: #0B2447;\n    box-shadow: 5px 5px 5px rgba(0.5, 0.5, 0.5, 0.5);\n    border-radius: 8px;\n    overflow: hidden;\n    transition: transform 0.1s;\n    display: flex;\n    flex-direction: column;\n    height: 100%;\n    margin: 5px; \n  }\n\n  .restaurant-item__header {\n    position: relative;\n  }\n\n  .restaurant-item .restaurant-item__header .restaurant-item__header__poster {\n    width: 100%;\n  }\n  .restaurant-item .restaurant-item__header__image{\n    max-width: 100%;\n    height: 300px; \n    object-fit: cover;\n  }\n\n  .restaurant-item .restaurant-item__header .restaurant-item__header__rating {\n    position: absolute;\n    padding: 8px;\n    bottom: 20px;\n    left: 0;\n    display: inline-block;\n    background-color: black;\n    color: white;\n  }\n  .restaurant-item:hover {\n    transform: scale(1.02);\n  }\n\n  .restaurant-item .restaurant-city {\n    font-size: 1rem;\n    color: #888;\n    margin-bottom: 10px;\n  }\n\n  .restaurant-item .restaurant-item__content {\n    padding: 16px;\n  }\n\n  .restaurant-item .restaurant-name {\n    color: white;\n    font-size: 1.5rem;\n    font-weight: bold;\n    margin-bottom: 10px;\n  }\n  .restaurant-item .restaurant-item__content h1 a {\n    color: white;\n    font-size: 1.5rem;\n    font-weight: bold;\n    margin-bottom: 10px;\n    text-decoration: none;\n  }\n  .restaurant-item .restaurant-item__content h3 {\n    font-size: 1rem;\n    color: #888;\n    margin-bottom: 10px;\n  }\n  .restaurant-item .restaurant-item__content .restaurant-rating{\n    font-size: 1rem;\n    color: #f8c10a;\n    margin-bottom: 10px;\n  }\n  .restaurant-item .restaurant-item__content p{\n    font-size: 1rem;\n    color: white;\n    overflow: hidden;\n    text-overflow: ellipsis;\n  }\n  .restaurant-item__content a {\n    display: inline-block;\n    width: 44px;\n    height: 44px;\n    text-decoration: none;\n    color: inherit;\n    font-size: 16px;\n    padding: 8px;\n    white-space: nowrap; \n    text-overflow: ellipsis; \n  }\n  \n  \n  /*\n    restaurant\n  */\n\n  .restaurant {\n    margin: 0 auto;\n    width: 100%;\n    max-width: 800px;\n    display: grid;\n    grid-template-columns: 1fr;\n    gap: 18px 16px;\n  }\n\n  .restaurant .restaurant__image {\n    width: 100%;\n    max-width: 400px;\n  }\n\n  .restaurant .restaurant__info h4 {\n    margin: 8px 0;\n  }\n\n  /* Detail */\n \n  .restaurant-detail {\n    background-color: white;\n    padding: 20px;\n    border-radius: 8px;\n    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);\n    margin: 20px;\n  }\n\n  /* Styling for the restaurant name */\n  .restaurant__name {\n    font-size: 24px;\n    font-weight: bold;\n    color: #0B2447;\n    margin-bottom: 10px;\n    align-items: center; \n    justify-content: center; \n    display: flex;\n  }\n\n  .content .content__heading {\n    margin-top: 20px;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    height: 100%; \n    text-align: center;\n  }\n\n \n  .restaurant__image {\n    width: 70%;\n    height: auto;\n    border-radius: 8px;\n    margin: 20px auto; \n    display: flex; \n    align-items: center; \n    justify-content: center; \n  }\n\n\n\n\n  .restaurant__info {\n    margin-bottom: 20px;\n  }\n\n  .restaurant__info h3 {\n    font-size: 20px;\n    font-weight: bold;\n    color: #0B2447;\n  }\n\n  .restaurant__info .movie-item__header__rating__score {\n    color: #f8c10a;\n  }\n\n\n  .restaurant__info h4 {\n    font-size: 16px;\n    color: #0B2447;\n    margin-top: 10px;\n  }\n\n\n  .restaurant__description {\n    margin-bottom: 20px;\n  }\n\n  .restaurant__description h3 {\n    font-size: 20px;\n    font-weight: bold;\n    color: #0B2447;\n  }\n\n  .restaurant__description p {\n    font-size: 16px;\n    color: #333;\n  }\n\n  \n  .restaurant__menu {\n    margin-bottom: 20px;\n  }\n\n  .restaurant__menu h3 {\n    font-size: 20px;\n    font-weight: bold;\n    color: #0B2447;\n  }\n\n  .restaurant__menu h4 {\n    font-size: 18px;\n    font-weight: bold;\n    color: #333;\n    margin-top: 10px;\n  }\n\n  .restaurant__menu ul {\n    list-style: none;\n    padding-left: 0;\n  }\n\n  .restaurant__menu li {\n    font-size: 16px;\n    color: #333;\n  }\n\n\n  .restaurant__reviews {\n    margin-bottom: 20px;\n  }\n\n  .restaurant__reviews h3 {\n    font-size: 20px;\n    font-weight: bold;\n    color: #0B2447;\n  }\n\n  .review-item {\n    margin-top: 20px;\n    padding: 10px;\n    background-color: #f0f0f0;\n    border-radius: 8px;\n  }\n\n  .review-item p {\n    font-size: 16px;\n    color: #333;\n  }\n\n  .review-item strong {\n    font-weight: bold;\n    color: #0B2447;\n  }\n\n  .restaurant-item__header__image {\n    max-width: 100%;\n    height: 300px;\n    object-fit: cover;\n  }\n\n  .restaurant-item:hover {\n    transform: scale(1.02);\n  }\n  #reviewFormContainer {\n    margin: 0 auto;\n    width: 50%;\n    padding: 20px;\n    background-color: #ffffff;\n    border: 1px solid #ccc;\n    border-radius: 15px;\n    box-shadow: 5px 5px 5px 5px rgba(0.5, 0.5, 0.5, 0.5);\n    margin-bottom: 30px;\n  }\n\n\n  #reviewFormContainer h2 {\n    text-align: center;\n    font-size: 24px;\n    margin-bottom: 20px;\n    color: #0B2447;\n  }\n\n  #reviewFormContainer label {\n    display: block; \n    text-align: center; \n    font-size: 16px;\n    font-weight: bold;\n    margin-top: 10px;\n    color: #444;\n  }\n  #reviewFormContainer input,\n  #reviewFormContainer textarea {\n    width: 100%;\n    padding: 10px;\n    font-size: 16px;\n    margin-top: 5px;\n    border: 1px solid #ccc;\n    border-radius: 5px;\n  }\n  #reviewFormContainer button {\n    background-color: #0B2447;\n    color: white;\n    border: none;\n    border-radius: 5px;\n    padding: 10px 20px;\n    font-size: 16px;\n    cursor: pointer;\n    margin-top: 20px;\n    display: inline-block;\n    min-width: 44px;\n    width: 100%; \n    height: 44px; \n    transition: background-color 0.3s ease;\n}\n\n\n\n  #reviewFormContainer button:hover {\n    background-color: black;\n  }\n\n\n\n  /*\n    Like\n  */\n\n  .like {\n    width: 55px;\n    height: 55px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    background-color: #db0000;\n    \n    position: fixed;\n    bottom: 16px;\n    right: 16px;\n    border-radius: 50%;\n    border: 0;\n    \n    font-size: 18px;\n    color: white;\n    cursor: pointer;\n  }\n\n  